  import { Card, CardBody, CardHeader } from "react-bootstrap";
  import { baseUrl } from "../../../../../utils";
  import React from "react";
  import AssetsbySkeleton from "../../layout/skeleton/assetsbySkeleton";
  import NoData from "../../layout/common/noData";

  const TopAiTags = (props) => {
    const { topaiTags, top_ai_loader } =props

    console.log(topaiTags,'topaiTagstopaiTagsdfsdf',top_ai_loader)

    return (
      <Card className="sw-card-custom gutter-b card-shadow">
        <CardHeader className="p-5">
          <h6 class="card-label font-weight-bolder m-0">
            <img
              src={baseUrl(`dashboard/analytic/ai.svg`)}
              height={"20px"}
              width={"20px"}
            />{" "}
            Top AI Tags
          </h6>
        </CardHeader>
        <CardBody className="p-5">

         { top_ai_loader ? <AssetsbySkeleton/> : topaiTags && Object.keys(topaiTags).length ?
           topaiTags.map((data,index)=>{
            return(<div className="px-5 py-2 border rounded mb-4">
              <div className="d-flex align-items-center justify-content-between">
                <div className="d-flex align-items-center me-10">
                  <div className="text-dark font-weight-boldest me-2">{index+1}.</div>
                  <div className="">
                    <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                       {data.tag}
                    </p>
                  </div>
                </div>
                <p className="count-tag border font-weight-boldest m-0">{data.count}</p>
              </div>
            </div>)
        }) :         
            <NoData/>
        }
        </CardBody>
      </Card>
    );
  };

 export default React.memo(TopAiTags);
